import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/about',
      name: 'about',
      component: function () { 
        return import(/* webpackChunkName: "about" */ './views/About.vue')
      }
    },
    {
      path: '/huntz',
      name: 'huntz',
      component: function () { 
        return import(/* webpackChunkName: "huntz" */ './views/huntzMini.vue')
      }
    },
    {
      path: '/imaginews',
      name: 'imaginews',
      component: function () { 
        return import(/* webpackChunkName: "imaginews" */ './views/imagiMini.vue')
      }
    },
    {
      path: '/nering',
      name: 'nering',
      component: function () { 
        return import(/* webpackChunkName: "nering" */ './views/neringMini.vue')
      }
    },
    {
      path: '/pilli',
      name: 'pilli',
      component: function () { 
        return import(/* webpackChunkName: "pilli" */ './views/pilliMini.vue')
      }
    },
    
  ],
  scrollBehavior () {
    return { x: 0, y: 0 }
  }
})
